import "../scss/songList.scss";

import React from "react";
import { Container, Row, Col, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import GoogleAuth from "../GoogleAuth";
import { connect } from "react-redux";
import { fetchSongs, createSong } from "../../actions";

class SongList extends React.Component {
  componentDidMount() {
    this.props.fetchSongs();
  }

  renderList() {
    return this.props.songs.map(song => {
      return (
        <article className="songItem" key={song.id}>
          <img src={song.enlaceImagen} alt="img" />
          <div className="songInfo">
            <span className="info-song">{song.cancion}</span>
            <span className="info-artist">{song.artista}</span>
            <a
              href={song.enlaceVideo}
              target="_blank"
              rel="noopener noreferrer"
            >
              ver video
            </a>
          </div>
          <div className="songAction">
            <Link to={`/song/edit/${song.id}`}>
              <Button variant="warning">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 24 24"
                  className="feather"
                >
                  <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path>
                  <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path>
                </svg>
              </Button>
            </Link>
            <Link to={`/song/delete/${song.id}`}>
              <Button variant="danger">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 24 24"
                  className="feather"
                >
                  <polyline points="3 6 5 6 21 6"></polyline>
                  <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
                  <line x1="10" y1="11" x2="10" y2="17"></line>
                  <line x1="14" y1="11" x2="14" y2="17"></line>
                </svg>
              </Button>
            </Link>
          </div>
        </article>
      );
    });
  }

  render() {
    if (!this.props.auth.isSignedIn) {
      return (
        <Container fluid={false}>
          <Row className="my-3">
            <Col>
              <h3>Usted no esta Autorizado</h3>
            </Col>
            <Col className="text-right">
              <GoogleAuth />
            </Col>
          </Row>
        </Container>
      );
    }
    return (
      <Container fluid={false}>
        <Row className="my-4">
          <Col>
            <h6>
              Favor de proporcionar este ID al desarrollador:{" "}
              {this.props.auth.userId}
            </h6>
          </Col>
        </Row>
        <Row className="my-4">
          <Col>
            <h3>Catalogo de canciones</h3>
          </Col>
          <Col className="songActions">
            <Link to="/song/new">
              <Button>Agregar canción</Button>
            </Link>
            <GoogleAuth />
          </Col>
        </Row>
        <Row>
          <section className="songList">{this.renderList()}</section>
        </Row>
      </Container>
    );
  }
}

const mapStateToPros = state => {
  return { songs: Object.values(state.song), auth: state.auth };
};

export default connect(mapStateToPros, { fetchSongs, createSong })(SongList);
