import "./scss/portfolio.scss";
import fondo from "../assets/img/f03.jpg";

import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { connect } from "react-redux";
import { fetchSongs } from "../actions";

import SongComponent from "./songs/SongComponent";

class Portfolio extends React.Component {
  componentDidMount() {
    this.props.fetchSongs();
  }

  renderList = () => {
    const listSongs = this.props.songs.sort((a, b) => {
      return new Date(a.fecha) - new Date(b.fecha);
    });

    return listSongs.map(song => {
      return <SongComponent key={song.id} {...song} />;
    });
  };

  render() {
    document.body.style.backgroundImage = `url(${fondo})`;

    return (
      <Container fluid={false}>
        <Row className="my-4">
          <Col>
            <header className="portfolio-title">Portafolio</header>
          </Col>
        </Row>
        <section className="portfolio">{this.renderList()}</section>
      </Container>
    );
  }
}

const mapStateToPros = state => {
  return { songs: Object.values(state.song) };
};

export default connect(mapStateToPros, { fetchSongs })(Portfolio);
