import "./scss/contact.scss";
import fondo from "../assets/img/f05.jpg";

import React from "react";
import { Container, Row, Col, Form, Button, Alert } from "react-bootstrap";
// import GoogleApiWrapper from "../utils/Maps";
import { Formik } from "formik";
import * as Yup from "yup";
import { connect } from "react-redux";
import { formSender } from "../actions";

class Contact extends React.Component {
  render() {
    document.body.style.backgroundImage = `url(${fondo})`;

    return (
      <Container fluid={false}>
        <Row className="my-4">
          <Col sm={12} md={{ span: 8, offset: 2 }}>
            <header className="contacto-title">CONTACTO</header>
          </Col>
        </Row>
        <Row>
          <Formik
            initialValues={{ nombre: "", telefono: "", email: "" }}
            validationSchema={Yup.object().shape({
              nombre: Yup.string()
                .min(5, "favor de ingresar su nombre real.")
                .required("este campo es requerido."),
              telefono: Yup.string()
                .min(10, "favor de ingresar su número con la clave lada.")
                .required("este campo es requerido."),
              email: Yup.string()
                .email(
                  "el correo proporcionado no cumple con el formato esperado."
                )
                .required("este campo es requerido.")
            })}
            onSubmit={(values, { setFieldError }) => {
              try {
                this.props.formSender(values);
              } catch (errors) {
                errors.forEach(err => {
                  setFieldError(err.field, err.error);
                });
              }
            }}
            component={this.form}
          />
          {/* <Col sm={12} md={6} className="mt-3">
            <div className="gmaps">
              <GoogleApiWrapper />
            </div>
          </Col> */}
        </Row>
      </Container>
    );
  }

  form = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    errors,
    touched
  }) => {
    return (
      <Col sm={12} md={{ span: 8, offset: 2 }}>
        <Form onSubmit={handleSubmit}>
          {/* formulario */}
          <Row>
            <Col>
              {this.props.formSent ? (
                <Alert variant="success">El correo ha sido enviado</Alert>
              ) : null}
              {this.props.formSent === false ? (
                <Alert variant="danger">
                  Parece tuvimos un problema, favor de volver a intentarlo
                  despues.
                </Alert>
              ) : null}
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group controlId="nombre">
                <Form.Label>Nombre</Form.Label>
                <Form.Control
                  type="text"
                  name="nombre"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.nombre}
                />
                {touched.nombre && errors.nombre ? (
                  <Form.Text className="text-muted">{errors.nombre}</Form.Text>
                ) : null}
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col sm={12} md={7} lg={8}>
              <Form.Group controlId="email">
                <Form.Label>Correo electrónico</Form.Label>
                <Form.Control
                  type="email"
                  name="email"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.email}
                />
                {touched.email && errors.email ? (
                  <Form.Text className="text-muted">{errors.email}</Form.Text>
                ) : null}
              </Form.Group>
            </Col>
            <Col sm={12} md={5} lg={4}>
              <Form.Group controlId="telefono">
                <Form.Label>Teléfono</Form.Label>
                <Form.Control
                  type="text"
                  name="telefono"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.telefono}
                  maxLength={10}
                />
                {touched.telefono && errors.telefono ? (
                  <Form.Text className="text-muted">
                    {errors.telefono}
                  </Form.Text>
                ) : null}
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Button variant="primary" type="submit">
                Enviar
              </Button>
            </Col>
          </Row>
        </Form>
      </Col>
    );
  };
}

const mapStateToProps = state => {
  return { formSent: state.mail.formSent };
};

export default connect(mapStateToProps, { formSender })(Contact);
