import "./scss/app.scss";

import React, { Component, Fragment, Suspense } from "react";
import { Router, Route } from "react-router-dom";

import history from "../history";

// import Animation from "./Animation";
import Header from "./Header";
import Principal from "./Principal";
import About from "./About";
import Portfolio from "./Portfolio";
import Equipment from "./Equipment";
import Contact from "./Contact";

import SongCreate from "./songs/SongCreate";
import SongEdit from "./songs/SongEdit";
import SongList from "./songs/SongList";
import SongDelete from "./songs/SongDelete";

class App extends Component {
  state = {
    isVisible: true,
    isNone: false
  };

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        isVisible: false
      });
    }, 5000);
    setTimeout(() => {
      this.setState({
        isNone: true
      });
    }, 5500);
  }

  render() {
    return (
      <Fragment>
        {/* <Animation visible={this.state.isVisible} none={this.state.isNone} /> */}
        <Router history={history}>
          <div className="backImage">
            <Suspense fallback="loading">
              <Header />
            </Suspense>
            <Route path="/" exact component={Principal} />
            <Route path="/about" component={About} />

            <Route path="/portfolio" component={Portfolio} />
            <Route path="/equipment" component={Equipment} />
            <Route path="/contact-us" component={Contact} />

            <Route path="/song/list" component={SongList} />
            <Route path="/song/new" component={SongCreate} />
            <Route path="/song/edit/:id" component={SongEdit} />
            <Route path="/song/delete/:id" component={SongDelete} />
          </div>
        </Router>
      </Fragment>
    );
  }
}

export default App;
