import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { connect } from "react-redux";
import { fetchSong, editSong } from "../../actions";

import SongForm from "./SongForm";

class SongEdit extends React.Component {
  componentDidMount() {
    this.props.fetchSong(this.props.match.params.id);
  }

  renderInitialValues = () => {
    const artista = this.props.songSelected.artista;
    const album = this.props.songSelected.album;
    const cancion = this.props.songSelected.cancion;
    const enlaceVideo = this.props.songSelected.enlaceVideo;
    const enlaceImagen = this.props.songSelected.enlaceImagen;
    const id = this.props.songSelected.id;

    return {
      artista: artista,
      album: album,
      cancion: cancion,
      enlaceVideo: enlaceVideo,
      enlaceImagen: enlaceImagen,
      id: id
    };
  };

  onSubmit = formValues => {
    this.props.editSong(formValues);
  };

  render() {
    if (!this.props.songSelected) {
      return <div>Loading...</div>;
    }

    return (
      <Container fluid={false}>
        <Row>
          <Col>
            <h3>Editar </h3>
          </Col>
        </Row>
        <SongForm
          initialValues={this.renderInitialValues()}
          onSubmit={this.onSubmit}
        />
      </Container>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return { songSelected: state.song[ownProps.match.params.id] };
};

export default connect(
  mapStateToProps,
  { fetchSong, editSong }
)(SongEdit);
