const SongComponent = props => {
  const renderSong = () => {
    return (
      <article className="song">
        <img
          className="song-image"
          src={props.enlaceImagen}
          alt={props.cancion}
        />
        <div className="song-content">
          <span>{props.cancion}</span>
          <span>{props.artista}</span>
        </div>
      </article>
    );
  };

  if (props.page !== "principal") {
    return (
      <React.Fragment>
        <a href={props.enlaceVideo} target="_blank" rel="noopener noreferrer">
          {renderSong()}
        </a>
      </React.Fragment>
    );
  }

  return (
    <React.Fragment>
      <Link to="/portfolio">{renderSong()}</Link>
    </React.Fragment>
  );
};

export default SongComponent;
