import React from "react";
import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import Modals from "../Modal";
import history from "../../history";
import { connect } from "react-redux";
import { fetchSong, deleteSong } from "../../actions";

class SongDelete extends React.Component {
  componentDidMount() {
    this.props.fetchSong(this.props.match.params.id);
  }

  renderActions() {
    const id = this.props.match.params.id;
    return (
      <React.Fragment>
        <Button variant="danger" onClick={() => this.props.deleteSong(id)}>
          Eliminar
        </Button>
        <Link to="/song/list">
          <Button variant="secondary">Cancelar</Button>
        </Link>
      </React.Fragment>
    );
  }

  renderContainer() {
    if (!this.props.songSelected) {
      return <p>Loading...</p>;
    }
    return (
      <p>
        ¿Desea eliminar la canción{" "}
        <span className="font-weight-bold">
          "{this.props.songSelected.cancion}"
        </span>{" "}
        de{" "}
        <span className="font-weight-bold">
          {this.props.songSelected.artista}
        </span>
        ?
      </p>
    );
  }

  render() {
    return (
      <Modals
        show={true}
        title="Eliminar canción"
        container={this.renderContainer()}
        actions={this.renderActions()}
        onDismiss={() => history.push("/song/list")}
      />
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return { songSelected: state.song[ownProps.match.params.id] };
};

export default connect(mapStateToProps, { fetchSong, deleteSong })(SongDelete);
